<template>
  <div>
    <!-- 文本 -->
    <TextContent
      v-if="msgOptions.type === MSG_ELEMENT_TYPE.TEXT"
      :text="msgOptions.text || msgOptions.content"
    />
    <!-- 回合制卡片消息 初诊及复诊 -->
    <div
      v-if="
        msgOptions.type === MSG_ELEMENT_TYPE.ROBOT_CARD ||
          msgOptions.type === 'round_msg'
      "
    >
      <p
        v-if="
          msgOptions.content[0].ask_type === 're_ask' ||
            msgOptions.content[0].ask_type === 'new_ask'
        "
        class="confirm_text"
      >
        请确认本次咨询主要诉求
      </p>
      <div
        v-for="(contentItem, ind) in msgOptions.content"
        :key="contentItem.ask_type ? contentItem.ask_type + ind : ind"
      >
        <BoutCard
          :title="msgOptions.title"
          :boutCard="
            boutCardData(contentItem, item.accountType, ind, msgOptions)
          "
          :msgTime="item.msgTime"
          @showDetail="showDetail"
        >
        </BoutCard>
        <p
          v-if="
            ind < msgOptions.content.length - 1 &&
              msgOptions.content[0].ask_type !== 're_ask'
          "
          :class="item.isSelfSend ? 'chat_divider' : 'chat_divider_left'"
        ></p>
      </div>
    </div>
    <!-- 图片 -->
    <div
      v-if="
        msgOptions.type === MSG_ELEMENT_TYPE.IMAGE ||
          msgOptions.type === 'image'
      "
      class="image_box"
    >
      <ImageContent :images-options="msgOptions" @thumbClick="thumbClick" />
    </div>
    <!-- 视频消息 -->
    <div v-if="msgOptions.type === MSG_ELEMENT_TYPE.VIDEO" class="image_box">
      <div class="msg_VideoContent">
        <img
          style="max-width:540px;"
          height="200"
          :src="
            msgOptions.url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
          "
          :data-src="
            msgOptions.url + '?x-oss-process=video/snapshot,t_0,w_0,h_0,f_jpg'
          "
          alt
        />
        <i
          class="el-icon-video-play"
          @click="thumbClick('video', msgOptions.url)"
        />
      </div>
    </div>
    <!-- 自定义卡片-->
    <CardContent
      v-if="msgOptions.type === MSG_ELEMENT_TYPE.CARD"
      :images-options="msgOptions"
      :baby="msgOptions.content.family_member"
      :symptom="msgOptions.content.ask_question_desc"
      :has-interven="msgOptions.content.has_interven"
      :interven-desc="msgOptions.content.interven_desc"
      :data-arr="msgOptions.content.ask_question_report"
      @showDetail="showDetail"
    />
    <!-- 语音消息 -->
    <my-audio
      v-if="msgOptions.type === MSG_ELEMENT_TYPE.VOICE"
      :msgtime="item.msgTime"
      :src="msgOptions.url"
      :vduration="msgOptions.duration"
    />
    <!-- 电话录音 -->
    <p
      v-if="
        msgOptions.show &&
          (msgOptions.type === 'voice_card' || msgOptions.is_card)
      "
    >
      <voice-card
        :msgtime="item.msgTime"
        :src="msgOptions.url"
        :vduration="msgOptions.duration"
      />
    </p>
    <!-- 链接 -->
    <LinkContent
      v-if="msgOptions.type === MSG_ELEMENT_TYPE.LINK"
      :link-options="msgOptions"
    />
    <!-- 评价详情 消息类型 -->
    <p v-if="msgOptions.type === 'comment'" class="align-left comment_content">
      用户评价：{{ (msgOptions && msgOptions.content) || '无' }}
    </p>
    <p
      v-if="msgOptions.type === 'suggestion'"
      class="align-left comment_content"
    >
      {{ msgOptions.bus_role_name }}建议：{{
        (msgOptions && msgOptions.suggestion) || '无'
      }}
    </p>
  </div>
</template>
<script>
import mywebim from '@/utils/im/mywebim.js' // 本地消息类型解析

import TextContent from './components/TextContent'
import ImageContent from './components/ImageContent'
import CardContent from './components/CardContent'
import LinkContent from './components/LinkContent'
import voiceCard from './components/voiceCard'
import BoutCard from './components/BoutCard'

import { boutCardData } from '@/filters'

export default {
  props: {
    msgOptions: {
      type: Object,
      default: () => {}
    },
    item: {
      type: Object,
      default: () => {}
    }
  },
  components: {
    TextContent,
    ImageContent,
    CardContent,
    LinkContent,
    voiceCard,
    BoutCard
  },
  data() {
    return {
      curUploadList: [],
      MSG_ELEMENT_TYPE: mywebim.MSG_ELEMENT_TYPE
    }
  },
  methods: {
    boutCardData,
    showDetail(val) {
      this.thumbClick(val.mediaType, val.meidiaUrl, val.list)
    },
    thumbClick(type, url, list) {
      if (this.$parent.thumbClickFun) {
        this.$parent.thumbClickFun(type, url, list)
      } else this.$emit('thumbClickFun', { type, url, list })
    }
  }
}
</script>
<style lang="scss" scoped>
.image_box {
  img {
    max-width: 540px;
  }
}
.comment_content {
  padding-top: 10px;
  line-height: 28px;
  font-size: 12px;
  color: #576277;
}
.msg_VideoContent {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 350px;
  text-align: center;
  img {
    max-width: 350px;
  }
  i.el-icon-video-play {
    position: absolute;
    top: 40%;
    font-size: 45px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}
</style>
