<template>
  <div ref="refmsgCard" class="msg-card">
    <div v-if="msgList && msgList.length > 0" class="infinite-list-wrapper">
      <ul>
        <li
          v-for="(item, index) in msgList"
          :key="index"
          style="line-height: 30px;"
        >
          <el-row :gutter="24" class="all">
            <el-col :span="24" :class="searchType + '-right'">
              <p class="color-999 align-left">
                <span v-if="showAdmin">{{
                  item.account && item.account.name
                }}</span>
                <span v-else>{{
                  item.from_account_type === 'User' ? '用户' : '服务团队'
                }}</span>
                <span class="fontsize-10 margin-left-20">{{
                  new Date(item.created_at) | formatTime()
                }}</span>
              </p>
              <noticeMsg
                v-if="item.message_body.type === 'notice'"
                :msgBody="item.message_body"
                :textAlign="'align-left'"
                :item="item"
              />
              <chatMsg
                v-else
                :msgOptions="item.message_body"
                :item="item"
                @thumbClickFun="thumbClickFunComment"
              />
            </el-col>
          </el-row>
        </li>
      </ul>
    </div>
    <div v-else style="text-align: center;color: #5a5e66;">
      暂无咨询记录
    </div>
    <monitor
      ref="monitor"
      :monitor-info="monitorInfo"
      :current-image-url-arr="curUploadUrlList"
    />
  </div>
</template>
<script>
import Monitor from '@/components/Monitor'
import chatMsg from '@/views/medical-service/diagnose/chat/chatArea/chatMsg'
import noticeMsg from '@/views/medical-service/diagnose/chat/chatArea/noticeMsg'
import _ from 'lodash'
export default {
  components: {
    Monitor,
    chatMsg,
    noticeMsg
  },
  props: {
    searchType: {
      type: String,
      default: 'all'
    },
    msgList: {
      type: Array,
      default: () => []
    },
    showAdmin: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      monitorInfo: {},
      curUploadUrlList: [],
      imageArr: []
    }
  },
  created() {
    console.log('msgList==', this.msgList)
  },
  methods: {
    thumbClickFun(type, url, list) {
      this.monitorInfo = {
        type,
        meta: { url }
      }
      if (list) {
        this.curUploadUrlList = list
      }
      if (
        this.curUploadUrlList &&
        this.curUploadUrlList.length &&
        type !== 'video'
      ) {
        const index = _.findIndex(this.curUploadUrlList, function(o) {
          return o === url
        })
        if (index === -1) {
          this.curUploadUrlList.push(url)
        }
        this.curUploadUrlList = _.sortedUniq(this.curUploadUrlList)
      } else if (type !== 'video') {
        this.curUploadUrlList.push(url)
      } else {
        this.curUploadUrlList = []
      }
      this.$refs.monitor.show()
    },
    thumbClickFunComment({ type, url, list }) {
      this.monitorInfo = {
        type,
        meta: { url }
      }
      if (list) {
        this.curUploadUrlList = list
      }
      if (
        this.curUploadUrlList &&
        this.curUploadUrlList.length &&
        type !== 'video'
      ) {
        const index = _.findIndex(this.curUploadUrlList, function(o) {
          return o === url
        })
        if (index === -1) {
          this.curUploadUrlList.push(url)
        }
        this.curUploadUrlList = _.sortedUniq(this.curUploadUrlList)
      } else if (type !== 'video') {
        this.curUploadUrlList.push(url)
      } else {
        this.curUploadUrlList = []
      }
      this.$refs.monitor.show()
    }
  }
}
</script>
<style lang="scss" scoped>
#user-table {
  width: 100%;
  height: 368px;
}
.infinite-list-wrapper {
  overflow-x: hidden;
  overflow-y: auto;
  height: 640px;
  background-color: #fff;
}
.cardVideoContent {
  position: relative;
  max-width: 350px;
  img {
    max-width: 290px;
    max-height: 260px;
  }
  i.el-icon-video-play {
    position: absolute;
    bottom: 40%;
    left: 31%;
    font-size: 35px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}
.VideoContent {
  position: relative;
  max-width: 350px;
  img {
    max-width: 290px;
    max-height: 260px;
  }
  i.el-icon-video-play {
    position: absolute;
    top: 30%;
    left: 30%;
    font-size: 35px;
    color: #fff;
    z-index: 1000;
    cursor: pointer;
  }
}
.msg-card {
  font-family: PingFangSC-Regular;
  .el-row.all {
    padding: 10px 15px 10px;
    &:first-child {
      border-top: 1px solid #e8eaec !important;
    }
    &:last-child {
      border-top: 1px solid #e8eaec !important;
    }
    &:hover {
      background: #f6f7fb;
    }
  }
  .image-box {
    position: relative;
    float: left;
    // display:inline-block;
    text-align: left;
    img {
      width: 100px;
      height: 100px;
    }
  }
}
</style>
