<template>
  <div>
    <div class="top-overview">
      <div class="detail-number">
        <span class="label">咨询编号：</span>{{ detail.fd_ask_ID }}
      </div>
      <div class="detail-package">
        <span class="label">套餐版本： </span>{{ detail.fd_service_type }}
      </div>
    </div>
    <div class="detail">
      <div class="detail__content">
        <div class="detail__content__tips">
          温馨提示：对用户承诺的评价方式为匿名评价，因此当出现对评价内容有疑问的情况，请注意与用户和医疗团队的沟通方式，注意保护用户隐私！
        </div>
        <div class="detail__content__rate">
          <div class="rate-title">
            用户评价
          </div>
          <div class="rate-content">
            <div class="rate-content__item">
              <div class="label">评价时间：</div>
              <div class="content">
                {{
                  detail.commets && detail.commets.length
                    ? formatTime(detail.commets[0].created_at)
                    : '无'
                }}
              </div>
            </div>
            <div v-for="(comment, index) in detail.commets" :key="index">
              <div class="rate-content__item">
                <div class="label">
                  对{{ comment.admin_user.name }}({{
                    systemRole(comment.bus_role, '')
                  }})的满意度：
                </div>
                <div class="content">{{ comment.star }}星</div>
              </div>
              <div class="rate-content__item">
                <div class="label">标签：</div>
                <div class="content">
                  <span v-if="comment.tags && comment.tags.length > 0">
                    <span
                      v-for="(tag, subindex) in comment.tags"
                      :key="`tag${subindex}`"
                    >
                      {{ tag }}
                    </span>
                  </span>
                  <span v-else>
                    无
                  </span>
                </div>
              </div>
            </div>
            <div class="multiple-line">
              <span class="label">评价详情：</span>
              <span
                v-if="
                  detail.commets &&
                    detail.commets.length &&
                    detail.commets[0].content
                "
                class="content"
                >{{ detail.commets && detail.commets[0].content }}</span
              >
              <span v-else>无</span>
            </div>
          </div>
        </div>
      </div>
      <div class="detail__history">
        <div class="detail__history__title">咨询记录</div>
        <msg-card :msg-list="detail.message_list" />
      </div>
    </div>
  </div>
</template>
<script>
import MsgCard from './components/MsgCard'
import { getMedicalServiceDetail } from '@/api/evaluate'
import { systemRole } from '@/utils/dictionary'
export default {
  name: 'MedicalServiceDetail',
  components: {
    MsgCard
  },
  data() {
    return {
      detail: {}
    }
  },
  mounted() {
    const evaluationId = this.$route.params.id
    getMedicalServiceDetail(evaluationId).then(res => {
      this.detail = res.data
    })
  },
  methods: {
    systemRole
  }
}
</script>
<style lang="scss" scoped>
@import '@/styles/evaluate/detail.scss';
</style>
