<template>
  <div class="notic-box">
    <p>
      {{ msgOptions.content }}
    </p>
    <el-row type="flex" v-if="operated" justify="center">
      <el-button
        size="mini"
        type="info"
        class="notic_disabled_btn margin-top-10 margin-bottom-10"
        :disabled="true"
      >
        {{ msgOptions.operateResult.result }}
      </el-button>
    </el-row>
    <el-row type="flex" v-if="!operated" justify="center">
      <el-button
        size="mini"
        type="primary"
        :disabled="btnIsDisabled"
        class="margin-top-10 margin-bottom-10"
        @click="confirmAccept"
      >
        确认
      </el-button>
    </el-row>
  </div>
</template>
<script>
export default {
  props: {
    operated: {
      type: Boolean,
      default: true
    },
    msgOptions: {
      type: Object,
      default: () => {}
    },
    btnIsDisabled: {
      type: Boolean,
      default: true
    },
    msgId: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  methods: {
    confirmAccept() {
      // 确认 发起转诊
      this.$emit('confirm', this.msgId)
    }
  }
}
</script>
