<template>
  <div>
    <!--notice 类型-->
    <!--todo的系统消息 -->
    <el-row
      type="flex"
      :justify="textAlign === 'align-center' ? 'center' : ''"
      v-if="msgBody.subType && msgBody.subType === 'todo'"
    >
      <!-- 用户转诊意向确认消息 -->
      <ReplyUser
        v-if="msgBody.noticeType === 'round_apply_transfer'"
        :replyContent="msgBody.adminDesc"
        :msg-id="item._id"
        :operated="msgBody.operated"
        :btnIsDisabled="btnIsDisabled(msgBody)"
        :userIntent="msgBody.userDesc"
      />

      <TransferContent
        v-if="
          (msgBody.noticeType && msgBody.noticeType === 'transfer_apply') ||
            msgBody.noticeType === 'transfer_reject'
        "
        :item="item"
        :msgOptions="msgBody"
        :bus-role="msgBody.bus_role"
        :btnIsDisabled="btnIsDisabled(msgBody)"
        :notice-type="msgBody.noticeType"
        :operated="msgBody.operated"
        :reason="msgBody.reason"
        :msg-id="item._id"
        @confirm="confirm"
      />
      <TransferAccept
        v-if="msgBody.noticeType && msgBody.noticeType === 'transfer_accept'"
        :class="textAlign"
        :msgId="item._id"
        :btnIsDisabled="btnIsDisabled(msgBody)"
        :operated="msgBody.operated"
        :msgOptions="msgBody"
        @confirm="confirm"
      />
    </el-row>
    <!-- 非todo类型的系统消息 -->
    <el-row
      type="flex"
      :justify="textAlign === 'align-center' ? 'center' : ''"
      v-if="!msgBody.subType || msgBody.subType !== 'todo'"
    >
      <SystemContent
        :class="textAlign"
        :msg-type="
          msgBody.subType && msgBody.subType === 'record'
            ? 'systemWarnning'
            : 'systemInfo'
        "
        :content="msgBody.content"
      />
    </el-row>
  </div>
</template>
<script>
import ReplyUser from './components/ReplyUser'
import TransferContent from './components/TransferContent'
import TransferAccept from './components/TransferAccept'
import SystemContent from './components/SystemContent'
import { mapGetters } from 'vuex'
import { confrimAccept } from '@/api/bout.js'

export default {
  props: {
    msgBody: {
      type: Object,
      default: () => {}
    },
    item: {
      type: Object,
      default: () => {}
    },
    textAlign: {
      type: String,
      default: 'align-center'
    }
  },
  components: {
    ReplyUser,
    TransferContent,
    TransferAccept,
    SystemContent
  },
  data() {
    return {}
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  methods: {
    btnIsDisabled(msg) {
      return !(msg.bus_role_id === this.userInfo.id)
    },
    confirm(msgId) {
      // 确认
      if (!msgId) {
        this.$message.info('messageId不能为空')
        return
      }
      const params = {
        result: '已确认' //后台要返回的按钮的文本
      }
      confrimAccept(msgId, params).then(res => {
        this.$message({
          type: res && res.data.result ? 'success' : 'error',
          message: res.message
        })
        this.$store.dispatch('chat/getLastAsk')
      })
    }
  }
}
</script>
