<template>
  <div class="linkContent">
    <p
      class="link"
      @click="toURL(linkOptions.url)"
      v-html="replaceURLWithHTMLLinks(linkOptions.content)"
    />
  </div>
</template>
<script>
import { replaceURLWithHTMLLinks } from '@/utils/index.js'
export default {
  name: 'ImageContent',
  props: {
    linkOptions: {
      type: Object,
      default: () => ({})
    }
  },
  created() {},
  methods: {
    replaceURLWithHTMLLinks,
    toURL(val) {
      if (val) {
        window.open(val)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.linkContent {
  max-width: 592px;
  word-break: break-all;
  text-align: left;
  a:hover {
    text-decoration: underline !important;
  }
}
</style>
