<template>
  <div class="reply-user-box">
    <p class="user_intent">
      {{ userIntent }}
    </p>
    <el-row v-if="operated">
      <span class="span_key">回复用户留言</span>
      <p class="notic-reason-box">
        {{ replyContent }}
      </p>
    </el-row>
    <div v-if="!operated">
      <el-row>
        <span class="span_key">回复用户留言</span>
        <el-input
          class="relpy_textarea"
          v-model="replyData"
          type="textarea"
          :rows="3"
          maxlength="100"
          show-word-limit
          placeholder="请输入内容"
        />
      </el-row>
      <el-row :gutter="24" type="flex" justify="center">
        <el-button
          class="btn-group"
          type="primary"
          size="mini"
          :disabled="!(btnIsDisabled === false && activedBaby.is_asking)"
          @click="confirmUserIntent"
        >
          确认
        </el-button>
      </el-row>
    </div>
  </div>
</template>
<script>
import { confirmUserIntent } from '@/api/bout'
import { mapGetters } from 'vuex'

export default {
  name: 'reply-user-box',
  props: {
    userIntent: {
      type: String,
      default: ''
    },
    operated: {
      type: Boolean,
      default: true
    },
    btnIsDisabled: {
      type: Boolean,
      default: true
    },
    replyContent: {
      type: String,
      default: ''
    },
    msgId: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      replyData: ''
    }
  },
  computed: {
    ...mapGetters(['activedBabyId', 'activedBaby', 'userInfo'])
  },
  methods: {
    confirmUserIntent() {
      if (!this.replyData) {
        this.$message.warning('回复内容不能为空')
        return
      }
      // 用户转诊意向确认
      confirmUserIntent(this.msgId, {
        desc: this.replyData,
        result: '已确认'
      }).then(res => {
        this.$message({
          type: res.data.result ? 'success' : 'warning',
          message: res.data.result ? '回复成功' : '回复失败'
        })
        if (res) {
          this.$store.dispatch('chat/getLastC2CHistoryMessages', '', {
            root: true
          })
          this.$store.dispatch('chat/getLastAsk')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.time {
  line-height: 16px;
  font-size: 12px;
  font-family: PingFangSC-Regular;
  text-align: center;
  color: #576277;
  letter-spacing: 0;
}
.relpy_textarea {
  margin-top: 10px;
}
.reply-user-box {
  margin: 10px auto;
  padding: 4px 30px 10px;
  max-width: 500px;
  background: rgba(188, 192, 209, 0.15);
  border-radius: 4px;
  .user_intent {
    line-height: 28px;
    font-weight: bold;
    color: #576277;
  }
  .span_key {
    display: inline-block;
    margin: 10px auto 0;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    color: #576277;
  }
  .btn-group {
    margin-top: 10px;
  }
}
</style>
