import request from '@/utils/request'

export function getFdAskList(query) {
  return request({
    url: '/comment_manager/fd_ask/list',
    method: 'post',
    data: query
  })
}
export function getList(query) {
  return request({
    url: '/comment_manager/single_ask/list',
    method: 'post',
    data: query
  })
}
// 咨询评论上下线
export function changeOnlineStatusFdAsk(params) {
  return request({
    url: '/comment_manager/fd_ask/updateStatus/' + params._id,
    method: 'put',
    data: params
  })
}
// 问诊评论上下线
export function changeOnlineStatus(params) {
  return request({
    url: '/comment_manager/single_ask/updateStatus/' + params._id,
    method: 'put',
    data: params
  })
}
// 根据评论id获取另一条评论的星级
export function getCommentStarById(id) {
  return request({
    url: '/comment_manager/fd_ask/getComment/' + id,
    method: 'get'
  })
}

// ￿详情页面的接口
