<template>
  <span class="SystemContent">
    <el-row>
      <p v-show="msgType == 'systemInfo'" class="color-yellow">
        <i class="el-icon-info" /><span>{{ content }}</span>
      </p>
      <p v-show="msgType == 'systemWarnning'" class="systemWarnning">
        {{ content }}
      </p>
    </el-row>
  </span>
</template>
<script>
export default {
  name: 'SystemContent',
  props: {
    content: {
      type: String,
      default: '超过十分钟'
    },
    msgType: {
      type: String,
      default: 'systemInfo' // 定义两种系统消息：一种时系统提示systemInfo，一种是系统警告systemWarnning
    }
  }
}
</script>
<style lang="scss" scoped>
.SystemContent {
  display: inline-block;
  margin-top: 4px;
  max-width: 580px;
  // text-align: center;
  .color-yellow {
    color: #f98611;
  }
  p {
    line-height: 16px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    text-align: center;
    color: #576277;
    letter-spacing: 0;
  }
  p.systemWarnning {
    padding: 8px 15px;
    color: #576277;
    background: rgba(188, 192, 209, 0.1);
    border-radius: 2px;
  }
}
</style>
