<template>
  <div class="transfer-apply-content">
    <el-row :gutter="24">
      <span class="transfer_key">{{ msgOptions.title }}</span>
    </el-row>
    <el-row :gutter="24">
      <span class="transfer_key">{{
        noticeType !== 'transfer_reject' ? '转诊原因：' : '拒绝原因：'
      }}</span>
      <p class="notic-reason-box">{{ msgOptions.reason || '无' }}</p>
      <p v-if="msgOptions.operateResult.result_text" class="padding-top-10">
        {{ msgOptions.operateResult.result_text }}
      </p>
      <el-row v-if="operated" type="flex" justify="center">
        <el-button
          size="mini"
          type="info"
          class="notic_disabled_btn margin-top-10"
          :disabled="true"
        >
          {{ msgOptions.operateResult.result }}
        </el-button>
      </el-row>
    </el-row>
    <el-row class="btn-group" v-if="operated === false">
      <el-button
        v-if="noticeType !== 'transfer_reject'"
        type="primary"
        :disabled="btnIsDisabled"
        size="mini"
        @click="acceptFun"
      >
        同意转诊
      </el-button>
      <el-button
        v-if="noticeType !== 'transfer_reject'"
        :disabled="btnIsDisabled"
        class="reject_btn"
        size="mini"
        @click="rejectFun"
      >
        无需转诊
      </el-button>
      <el-button
        v-if="noticeType === 'transfer_reject'"
        :disabled="btnIsDisabled"
        type="primary"
        size="mini"
        @click="confirmAccept"
      >
        确认
      </el-button>
    </el-row>
    <el-row
      v-if="showReason && rejectRequest"
      :gutter="24"
      class="margin-top-10"
    >
      <span class="transfer_key">
        <i style="color:#FF4E4E;margin-bottom:5px;" class="el-icon-info"></i
        >拒绝原因：
      </span>
      <span class="transfer_value">
        <el-input
          v-model="reject_reason"
          style="width:100%"
          type="textarea"
          :rows="3"
          maxlength="100"
          show-word-limit
          placeholder="请输入拒绝健康管理师转诊请求的原因"
        />
      </span>
    </el-row>
  </div>
</template>
<script>
import { rejectApi, acceptApi } from '@/api/diagnose'
import { mapGetters } from 'vuex'

export default {
  name: 'TransferContent',
  props: {
    item: {
      type: Object,
      default: () => {}
    },
    msgOptions: {
      type: Object,
      default: () => {}
    },
    busRole: {
      type: String,
      default: ''
    },
    btnIsDisabled: {
      type: Boolean,
      default: true
    },
    noticeType: {
      type: String,
      default: ''
    },
    operated: {
      // 按钮是否可操作，false则可操作
      type: Boolean,
      default: true
    },
    msgId: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      reject_reason: '',
      showReason: false,
      rejectRequest: true
    }
  },
  computed: {
    ...mapGetters(['lastAsk', 'userInfo'])
  },
  methods: {
    confirmAccept() {
      this.$emit('confirm', this.msgId)
    },
    rejectFun() {
      if (this.showReason) {
        const params = {
          message_id: this.msgId,
          transfer_reject_reason: this.reject_reason
        }
        if (this.reject_reason) {
          rejectApi(this.item.fdAsk, params).then(res => {
            const operName = '拒绝转诊'
            this.$message({
              type: res.data.result ? 'success' : 'error',
              message: res.data.result ? operName + '成功' : operName + '失败'
            })
            if (res.data.result) {
              this.rejectRequest = false
            } else {
              this.rejectRequest = true
            }
          })
        }
      } else if (!this.showReason) {
        this.showReason = true
      }
    },
    acceptFun() {
      if (!this.item.fdAsk) {
        this.$message({
          type: 'success',
          message: '同意转诊失败，请重试'
        })
        return
      }
      acceptApi(this.item.fdAsk, { message_id: this.msgId }).then(res => {
        this.$message({
          type: res.data.result ? 'success' : 'error',
          message: res.data.result ? '同意转诊成功' : '同意转诊失败'
        })
        if (res.data.result) {
          this.$store.dispatch('chat/getLastC2CHistoryMessages', '', {
            root: true
          })
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.padding-top-10 {
  font-weight: bold;
  color: #576277;
}
.transfer-apply-content {
  margin: 10px auto;
  padding: 10px 30px;
  min-width: 250px;
  max-width: 620px;
  white-space: pre-line;
  background: rgba(188, 192, 209, 0.15);
  border-radius: 4px;
  word-break: break-all;
  .el-row:not(:last-child) {
    margin-bottom: 10px;
  }
  .transfer_key {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: bold;
    color: #576277;
    letter-spacing: 0;
  }
  .transfer_value {
    line-height: 20px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    text-align: justify;
    color: #576277;
    letter-spacing: 0;
    word-break: break-all;
  }
  .btn-group {
    text-align: center;
    .el-button.reject_btn {
      color: #0085ff;
      background: #fff;
      border: 1px solid #0085ff;
      border-radius: 4px;
      &:hover,
      &:active {
        color: #409eff;
        background: #fff;
      }
    }
  }
}
</style>
