<template>
  <img
    height="300"
    style="max-width:540px;"
    alt=""
    :src="
      imagesOptions.images ? imagesOptions.images.oriImage : imagesOptions.url
    "
    @click="thumbClickFun"
  />
</template>
<script>
export default {
  name: 'ImageContent',
  props: {
    imagesOptions: {
      type: Object,
      default: () => ({})
    }
  },
  created() {},
  methods: {
    thumbClickFun() {
      if (this.imagesOptions.images)
        this.$emit('thumbClick', 'image', this.imagesOptions.images.oriImage)
      else this.$emit('thumbClick', 'image', this.imagesOptions.url)
    }
  }
}
</script>
